.App {
    text-align: center;
    position: relative;
  
    
  }
  
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
  }
  
  .App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
  }
  
  .App-title {
    font-size: 1.5em;
  }
  
  .App-intro {
    font-size: large;
  }
  
  .background {
    /* position: absolute; */
    z-index: -1;
    size: 100%;
    opacity: .4;
  }

  .button {
     position: absolute;
     bottom: 1%;
     left: 48%;
     border-top: 1px solid #e6f0f7;
     background: #cad9e3;
     background: -webkit-gradient(linear, left top, left bottom, from(#5b9dc9), to(#cad9e3));
     background: -webkit-linear-gradient(top, #5b9dc9, #cad9e3);
     background: -o-linear-gradient(top, #5b9dc9, #cad9e3);
     padding: 5px 10px;
     border-radius: 8px;
     -webkit-box-shadow: rgba(0,0,0,1) 0 1px 0;
     box-shadow: rgba(0,0,0,1) 0 1px 0;
     text-shadow: rgba(0,0,0,.4) 0 1px 0;
     color: #000000;
     font-size: 14px;
     font-family: Georgia, serif;
     text-decoration: none;
     vertical-align: middle;
     }
   .button:hover {
     border-top-color: #28597a;
     background: #28597a;
     color: #ccc;
     }
   .button:active {
     border-top-color: #1b435e;
     background: #1b435e;
     }

    .email {
       margin: 20px;
       position: absolute;
       left: 35%;
       top: 75%;
       width: 25%;
       text-align: center;
       display: inline-block;
       -webkit-box-sizing: content-box;
       box-sizing: content-box;
       padding: 6px 20px;
       border: 2px, black;
       border-radius: 32px / 54px;
        font: normal 18px/normal "Coda", Helvetica, sans-serif;
       color: rgba(0,142,198,1);
       -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
       background: rgba(252,252,252,1);
       -webkit-box-shadow: 0 1px 2px 0 rgba(0,0,0,0.2) inset;
       box-shadow: 0 1px 2px 0 rgba(0,0,0,0.2) inset;
       text-shadow: 1px 1px 0 rgba(255,255,255,0.66) ;
       -webkit-transition: all 200ms cubic-bezier(0.42, 0, 0.58, 1);
       -o-transition: all 200ms cubic-bezier(0.42, 0, 0.58, 1);
       transition: all 200ms cubic-bezier(0.42, 0, 0.58, 1);
       }
       
       .form:hover {
         background: rgb(234, 245, 248);
         -webkit-box-shadow: 0 2px 2px 0 rgba(90,90,90,0.2) inset;
         box-shadow: 0 2px 2px 0 rgba(90,90,90,0.2) inset;
       }
       
       .form:focus {
         background: rgb(247, 246, 236);
       } 
      
      .form input {
        display: block;
        width: 100%;
      } 

   .password {
        margin: 20px;
        position: absolute;
        left: 35%;
        top: 85%;
        width: 25%;
        text-align: center;
        display: inline-block;
        -webkit-box-sizing: content-box;
        box-sizing: content-box;
        padding: 6px 20px;
        border: 2px, black;
        border-radius: 32px / 54px;
         font: normal 18px/normal "Coda", Helvetica, sans-serif;
        color: rgba(0,142,198,1);
        -o-text-overflow: ellipsis;
        text-overflow: ellipsis;
        background: rgba(252,252,252,1);
        -webkit-box-shadow: 0 1px 2px 0 rgba(0,0,0,0.2) inset;
        box-shadow: 0 1px 2px 0 rgba(0,0,0,0.2) inset;
        text-shadow: 1px 1px 0 rgba(255,255,255,0.66) ;
        -webkit-transition: all 200ms cubic-bezier(0.42, 0, 0.58, 1);
        -o-transition: all 200ms cubic-bezier(0.42, 0, 0.58, 1);
        transition: all 200ms cubic-bezier(0.42, 0, 0.58, 1);
        }
        
        .form-p:hover {
          background: rgba(234,245,248);
          -webkit-box-shadow: 0 2px 2px 0 rgba(90,90,90,0.2) inset;
          box-shadow: 0 2px 2px 0 rgba(90,90,90,0.2) inset;
        }
        
        .form-p:focus {
          background: rgba(255,253,232,1);
        }   
  
  .subtitle {
    position: absolute;
    left: 0;
    top: 55%;
    width: 100%;
    text-align: center;
    font-size: 25px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
  }
  
  .title {
    position: absolute;
    left: 0;
    top: 35%;
    width: 100%;
    text-align: center;
    font-size: 118px;
    color: rgb(139, 9, 35);
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  }
  
  @-webkit-keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  


